.section-achivements {
  background-color: $color-dark-theme;

  .title-item {
    position: relative;
    font-weight: 400;
    padding-bottom: 10px;

    &:hover,
    &:focus {
      font-weight: 900;
      cursor: pointer;
    }
  }

  .current::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 4px;
    background: linear-gradient($gradient-color);
    border-radius: 2px;
  }
}

.list-achivements-btn {
  display: flex;
  flex-basis: calc (100%/3);
  justify-content: center;
  padding-bottom: 50px;
}

.item-achivements-btn:not(:last-child) {
  margin-right: 100px;
}