/* Слайдер */
.slick-slider{
	min-width: 0;
}
/* Ограничивающая оболочка */
.slick-list {
	overflow: hidden;
}
/* Лента слайдов */
.slick-track{
	display: flex;
	align-items: flex-start;
}

/* Слайд */
.slick-slide{}
/* Слайд активный */
.slick-slide.slick-active{}
/* Слайд основной */
.slick-slide.slick-current{}
/* Слайд по центру */
.slick-slide.slick-center{}

/* Стрелка */
.slick-arrow{}
/* Стрелка влево */
.slick-arrow.slick-prev{}
/* Стрелка вправо */
.slick-arrow.slick-next{}
/* Стрелка не активная */
.slick-arrow.slick-disabled{}

/* Точки */
.slick-dots{}
.slick-dots li{}
.slick-dots button{}

/* Конкретный слайдер */
.slider {
	position: relative;
	padding: 0px 60px;
}

@media (max-width:767px){
	.slider {
		padding: 0px 40px;
	}
}

.slider .slick-arrow{
	position: absolute;
	top: 50%;
	// margin: -60px 0px 0px 0px;
	z-index: 10;
	font-size: 0;
	width: 30px;
	height: 60px;
}
.slider .slick-arrow.slick-prev{
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background: url('../images/slider/sl-arrow_l.svg') 0 0 / 100% no-repeat;
  border: none;
}
.slider .slick-arrow.slick-next{
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background: url('../images/slider/sl-arrow_r.svg') 0 0 / 100% no-repeat;
  border: none;
}
.slider .slick-arrow.slick-disabled{
	opacity: 0.2;
}

.slider .slick-dots{
	display: flex;
	align-items: center;
	justify-content: center;
}
.slider .slick-dots li{
	list-style: none;
	margin: 0px 10px;
}
.slider .slick-dots button{
  font-size: 0;
  margin-top: 30px;
	width: 10px;
	height: 10px;
  background-color: $main-color;
  border: none;
	border-radius: 50%;
}

.slider .slick-dots li.slick-active button{
	background-color: transparent;
	border: 1px solid $main-color;
}

.item-slider {
	padding: 0px 15px;
}
.item-slider img{
  max-width: 100%;
  margin-top: auto;
  margin-bottom: auto;
}
.slider__item.slick-center{	
}

